'use client';

import React, { useState, useEffect } from 'react';
import { Plus, Settings, Save, RotateCcw, Play, Upload, Download, Sparkles, Grid3x3, AlertTriangle, Layout, Clock, CheckCircle, Palette } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Separator } from '@/components/ui/separator';
import { Badge } from '@/components/ui/badge';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { AppLayout } from '@/components/layout/AppLayout';

// Types and interfaces
interface Table {
  id: number;
  number: string;
  seats: number;
  status: 'available' | 'occupied' | 'reserved' | 'out-of-service';
  zone?: string;
  shape: 'circle' | 'square' | 'rectangle';
  x: number;
  y: number;
  width: number;
  height: number;
}

interface Zone {
  id: string;
  name: string;
  color: string;
  tables: number[];
}

export default function TableManagementPage() {
  const [tables, setTables] = useState<Table[]>([]);
  const [zones, setZones] = useState<Zone[]>([]);
  const [selectedTable, setSelectedTable] = useState<Table | null>(null);
  const [activeTab, setActiveTab] = useState('layout');
  const [isDraftMode, setIsDraftMode] = useState(false);
  const [validationErrors, setValidationErrors] = useState<string[]>([]);
  const [history, setHistory] = useState<{ tables: Table[]; zones: Zone[] }[]>([]);

  useEffect(() => {
    loadTableConfiguration();
  }, []);

  const loadTableConfiguration = () => {
    // Load saved configuration or initialize with defaults
    const defaultTables: Table[] = [
      { id: 1, number: '1', seats: 4, status: 'available', zone: 'main', shape: 'circle', x: 100, y: 100, width: 80, height: 80 },
      { id: 2, number: '2', seats: 2, status: 'occupied', zone: 'main', shape: 'square', x: 200, y: 100, width: 60, height: 60 },
      { id: 3, number: '3', seats: 6, status: 'available', zone: 'patio', shape: 'rectangle', x: 300, y: 100, width: 100, height: 60 },
    ];

    const defaultZones: Zone[] = [
      { id: 'main', name: 'Main Dining', color: '#3b82f6', tables: [1, 2] },
      { id: 'patio', name: 'Patio', color: '#10b981', tables: [3] },
    ];

    setTables(defaultTables);
    setZones(defaultZones);
  };

  const saveConfiguration = () => {
    const errors = validateConfiguration();
    if (errors.length > 0) {
      setValidationErrors(errors);
      return;
    }

    // Save configuration logic here
    console.log('Saving configuration...', { tables, zones });
    setValidationErrors([]);
    setIsDraftMode(false);
  };

  const validateConfiguration = (): string[] => {
    const errors: string[] = [];
    
    // Check for duplicate table numbers
    const tableNumbers = tables.map(t => t.number);
    const duplicateNumbers = tableNumbers.filter((num, index) => tableNumbers.indexOf(num) !== index);
    if (duplicateNumbers.length > 0) {
      errors.push(`Duplicate table numbers: ${duplicateNumbers.join(', ')}`);
    }

    // Check for overlapping tables
    for (let i = 0; i < tables.length; i++) {
      for (let j = i + 1; j < tables.length; j++) {
        const table1 = tables[i];
        const table2 = tables[j];
        
        if (
          table1.x < table2.x + table2.width &&
          table1.x + table1.width > table2.x &&
          table1.y < table2.y + table2.height &&
          table1.y + table1.height > table2.y
        ) {
          errors.push(`Tables ${table1.number} and ${table2.number} are overlapping`);
        }
      }
    }

    return errors;
  };

  const addTable = () => {
    const newTable: Table = {
      id: Math.max(...tables.map(t => t.id), 0) + 1,
      number: (tables.length + 1).toString(),
      seats: 4,
      status: 'available',
      zone: zones[0]?.id || 'main',
      shape: 'circle',
      x: 50,
      y: 50,
      width: 80,
      height: 80,
    };

    setTables(prev => [...prev, newTable]);
    setIsDraftMode(true);
  };

  const updateTable = (id: number, updates: Partial<Table>) => {
    setTables(prev => prev.map(table => 
      table.id === id ? { ...table, ...updates } : table
    ));
    setIsDraftMode(true);
  };

  const deleteTable = (id: number) => {
    setTables(prev => prev.filter(table => table.id !== id));
    setSelectedTable(null);
    setIsDraftMode(true);
  };

  const addZone = () => {
    const newZone: Zone = {
      id: `zone-${zones.length + 1}`,
      name: `Zone ${zones.length + 1}`,
      color: '#6366f1',
      tables: [],
    };

    setZones(prev => [...prev, newZone]);
    setIsDraftMode(true);
  };

  const updateZone = (id: string, updates: Partial<Zone>) => {
    setZones(prev => prev.map(zone => 
      zone.id === id ? { ...zone, ...updates } : zone
    ));
    setIsDraftMode(true);
  };

  const deleteZone = (id: string) => {
    // Move tables from deleted zone to unassigned
    setTables(prev => prev.map(table => 
      table.zone === id ? { ...table, zone: undefined } : table
    ));
    setZones(prev => prev.filter(zone => zone.id !== id));
    setIsDraftMode(true);
  };

  const resetLayout = () => {
    if (history.length > 0) {
      const lastState = history[history.length - 1];
      setTables(lastState.tables);
      setZones(lastState.zones);
      setHistory(prev => prev.slice(0, -1));
    }
  };

  const getTableStatusColor = (status: Table['status']) => {
    switch (status) {
      case 'available': return '#10b981';
      case 'occupied': return '#ef4444';
      case 'reserved': return '#f59e0b';
      case 'out-of-service': return '#6b7280';
      default: return '#3b82f6';
    }
  };

  return (
    <AppLayout title="Table Management">
      <div className="space-y-6">
        {/* Header */}
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-3xl font-bold bg-gradient-to-r from-blue-600 to-purple-600 bg-clip-text text-transparent">
              Table Management
            </h1>
            <p className="text-gray-600 mt-1">Design and manage your dining area layout</p>
          </div>
          
          <div className="flex items-center gap-3">
            {isDraftMode && (
              <Badge variant="outline" className="bg-yellow-50 text-yellow-700 border-yellow-300">
                <Clock className="w-3 h-3 mr-1" />
                Draft Mode
              </Badge>
            )}
            
            <Button 
              variant="outline" 
              onClick={resetLayout}
              disabled={history.length === 0}
              className="flex items-center gap-2"
            >
              <RotateCcw className="w-4 h-4" />
              Undo
            </Button>
            
            <Button 
              onClick={saveConfiguration}
              className="bg-gradient-to-r from-blue-600 to-purple-600 hover:from-blue-700 hover:to-purple-700 flex items-center gap-2"
            >
              <Save className="w-4 h-4" />
              Save Layout
            </Button>
          </div>
        </div>

        {/* Validation Errors */}
        {validationErrors.length > 0 && (
          <Card className="border-red-200 bg-red-50">
            <CardContent className="pt-4">
              <div className="flex items-start gap-3">
                <AlertTriangle className="w-5 h-5 text-red-500 flex-shrink-0 mt-0.5" />
                <div>
                  <h3 className="font-medium text-red-800 mb-2">Configuration Issues</h3>
                  <ul className="text-sm text-red-700 space-y-1">
                    {validationErrors.map((error, index) => (
                      <li key={index}>• {error}</li>
                    ))}
                  </ul>
                </div>
              </div>
            </CardContent>
          </Card>
        )}

        {/* Main Content */}
        <Tabs value={activeTab} onValueChange={setActiveTab} className="space-y-6">
          <TabsList className="grid w-full grid-cols-3">
            <TabsTrigger value="layout" className="flex items-center gap-2">
              <Layout className="w-4 h-4" />
              Layout Designer
            </TabsTrigger>
            <TabsTrigger value="zones" className="flex items-center gap-2">
              <Grid3x3 className="w-4 h-4" />
              Zone Management
            </TabsTrigger>
            <TabsTrigger value="settings" className="flex items-center gap-2">
              <Settings className="w-4 h-4" />
              Settings
            </TabsTrigger>
          </TabsList>

          {/* Layout Designer Tab */}
          <TabsContent value="layout" className="space-y-6">
            <div className="grid grid-cols-1 lg:grid-cols-4 gap-6">
              {/* Layout Canvas */}
              <Card className="lg:col-span-3">
                <CardHeader>
                  <div className="flex items-center justify-between">
                    <CardTitle className="flex items-center gap-2">
                      <Layout className="w-5 h-5" />
                      Dining Area Layout
                    </CardTitle>
                    <Button onClick={addTable} className="flex items-center gap-2">
                      <Plus className="w-4 h-4" />
                      Add Table
                    </Button>
                  </div>
                </CardHeader>
                <CardContent>
                  <div className="relative bg-gray-50 border-2 border-dashed border-gray-300 rounded-lg" style={{ height: '500px' }}>
                    {/* Render tables */}
                    {tables.map((table) => (
                      <div
                        key={table.id}
                        className={`absolute cursor-pointer transition-all duration-200 border-2 flex items-center justify-center text-sm font-medium text-white shadow-lg hover:shadow-xl ${
                          selectedTable?.id === table.id ? 'ring-2 ring-blue-500 ring-offset-2' : ''
                        }`}
                        style={{
                          left: table.x,
                          top: table.y,
                          width: table.width,
                          height: table.height,
                          backgroundColor: getTableStatusColor(table.status),
                          borderRadius: table.shape === 'circle' ? '50%' : table.shape === 'square' ? '8px' : '12px',
                        }}
                        onClick={() => setSelectedTable(table)}
                      >
                        {table.number}
                      </div>
                    ))}
                  </div>
                </CardContent>
              </Card>

              {/* Table Properties Panel */}
              <Card>
                <CardHeader>
                  <CardTitle className="text-lg">Table Properties</CardTitle>
                </CardHeader>
                <CardContent className="space-y-4">
                  {selectedTable ? (
                    <>
                      <div>
                        <label className="text-sm font-medium mb-1 block">Table Number</label>
                        <Input
                          value={selectedTable.number}
                          onChange={(e) => updateTable(selectedTable.id, { number: e.target.value })}
                        />
                      </div>
                      
                      <div>
                        <label className="text-sm font-medium mb-1 block">Seats</label>
                        <Input
                          type="number"
                          value={selectedTable.seats}
                          onChange={(e) => updateTable(selectedTable.id, { seats: parseInt(e.target.value) || 1 })}
                        />
                      </div>

                      <div>
                        <label className="text-sm font-medium mb-1 block">Status</label>
                        <select
                          value={selectedTable.status}
                          onChange={(e) => updateTable(selectedTable.id, { status: e.target.value as Table['status'] })}
                          className="w-full p-2 border border-gray-300 rounded-md"
                        >
                          <option value="available">Available</option>
                          <option value="occupied">Occupied</option>
                          <option value="reserved">Reserved</option>
                          <option value="out-of-service">Out of Service</option>
                        </select>
                      </div>

                      <div>
                        <label className="text-sm font-medium mb-1 block">Shape</label>
                        <select
                          value={selectedTable.shape}
                          onChange={(e) => updateTable(selectedTable.id, { shape: e.target.value as Table['shape'] })}
                          className="w-full p-2 border border-gray-300 rounded-md"
                        >
                          <option value="circle">Circle</option>
                          <option value="square">Square</option>
                          <option value="rectangle">Rectangle</option>
                        </select>
                      </div>

                      <Separator />

                      <Button
                        variant="destructive"
                        onClick={() => deleteTable(selectedTable.id)}
                        className="w-full"
                      >
                        Delete Table
                      </Button>
                    </>
                  ) : (
                    <p className="text-gray-500 text-center py-8">
                      Select a table to edit its properties
                    </p>
                  )}
                </CardContent>
              </Card>
            </div>
          </TabsContent>

          {/* Zone Management Tab */}
          <TabsContent value="zones" className="space-y-6">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              {zones.map((zone) => (
                <Card key={zone.id}>
                  <CardHeader>
                    <div className="flex items-center justify-between">
                      <CardTitle className="flex items-center gap-2">
                        <div
                          className="w-4 h-4 rounded"
                          style={{ backgroundColor: zone.color }}
                        />
                        {zone.name}
                      </CardTitle>
                      <Button
                        variant="destructive"
                        size="sm"
                        onClick={() => deleteZone(zone.id)}
                      >
                        Delete
                      </Button>
                    </div>
                  </CardHeader>
                  <CardContent className="space-y-4">
                    <div>
                      <label className="text-sm font-medium mb-1 block">Zone Name</label>
                      <Input
                        value={zone.name}
                        onChange={(e) => updateZone(zone.id, { name: e.target.value })}
                      />
                    </div>
                    
                    <div>
                      <label className="text-sm font-medium mb-1 block">Color</label>
                      <Input
                        type="color"
                        value={zone.color}
                        onChange={(e) => updateZone(zone.id, { color: e.target.value })}
                      />
                    </div>

                    <div>
                      <label className="text-sm font-medium mb-1 block">Tables in Zone</label>
                      <div className="flex flex-wrap gap-1">
                        {tables
                          .filter(table => table.zone === zone.id)
                          .map(table => (
                            <Badge key={table.id} variant="secondary">
                              Table {table.number}
                            </Badge>
                          ))}
                      </div>
                    </div>
                  </CardContent>
                </Card>
              ))}

              <Card className="border-dashed">
                <CardContent className="flex items-center justify-center py-12">
                  <Button onClick={addZone} className="flex items-center gap-2">
                    <Plus className="w-4 h-4" />
                    Add New Zone
                  </Button>
                </CardContent>
              </Card>
            </div>
          </TabsContent>

          {/* Settings Tab */}
          <TabsContent value="settings" className="space-y-6">
            <Card>
              <CardHeader>
                <CardTitle className="flex items-center gap-2">
                  <Settings className="w-5 h-5" />
                  Layout Settings
                </CardTitle>
              </CardHeader>
              <CardContent className="space-y-6">
                <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                  <div className="space-y-4">
                    <h3 className="font-medium">Import/Export</h3>
                    <div className="space-y-2">
                      <Button variant="outline" className="w-full flex items-center gap-2">
                        <Upload className="w-4 h-4" />
                        Import Layout
                      </Button>
                      <Button variant="outline" className="w-full flex items-center gap-2">
                        <Download className="w-4 h-4" />
                        Export Layout
                      </Button>
                    </div>
                  </div>

                  <div className="space-y-4">
                    <h3 className="font-medium">Quick Actions</h3>
                    <div className="space-y-2">
                      <Button variant="outline" className="w-full flex items-center gap-2">
                        <Sparkles className="w-4 h-4" />
                        Auto-Optimize Layout
                      </Button>
                      <Button variant="outline" className="w-full flex items-center gap-2">
                        <CheckCircle className="w-4 h-4" />
                        Validate Configuration
                      </Button>
                    </div>
                  </div>
                </div>
              </CardContent>
            </Card>
          </TabsContent>
        </Tabs>
      </div>
    </AppLayout>
  );
}
import { Plus, Settings, Save, RotateCcw, Play, Upload, Download, Sparkles, Grid3x3, AlertTriangle, Layout, Clock, CheckCircle, Palette } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Separator } from '@/components/ui/separator';
import { Badge } from '@/components/ui/badge';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { AppLayout } from '@/components/layout/AppLayout';

// Types and interfaces
interface Table {
  id: number;
  number: string;
  seats: number;
  status: 'available' | 'occupied' | 'reserved' | 'out-of-service';
  zone?: string;
  shape: 'circle' | 'square' | 'rectangle';
  x: number;
  y: number;
  width: number;
  height: number;
}

interface Zone {
  id: string;
  name: string;
  color: string;
  tables: number[];
}

export default function TableManagementPage() {
  const [tables, setTables] = useState<Table[]>([]);
  const [zones, setZones] = useState<Zone[]>([]);
  const [selectedTable, setSelectedTable] = useState<Table | null>(null);
  const [activeTab, setActiveTab] = useState('layout');
  const [isDraftMode, setIsDraftMode] = useState(false);
  const [validationErrors, setValidationErrors] = useState<string[]>([]);
  const [history, setHistory] = useState<{ tables: Table[]; zones: Zone[] }[]>([]);

  useEffect(() => {
    loadTableConfiguration();
  }, []);

  const loadTableConfiguration = () => {
    // Load saved configuration or initialize with defaults
    const defaultTables: Table[] = [
      { id: 1, number: '1', seats: 4, status: 'available', zone: 'main', shape: 'circle', x: 100, y: 100, width: 80, height: 80 },
      { id: 2, number: '2', seats: 2, status: 'occupied', zone: 'main', shape: 'square', x: 200, y: 100, width: 60, height: 60 },
      { id: 3, number: '3', seats: 6, status: 'available', zone: 'patio', shape: 'rectangle', x: 300, y: 100, width: 100, height: 60 },
    ];

    const defaultZones: Zone[] = [
      { id: 'main', name: 'Main Dining', color: '#3b82f6', tables: [1, 2] },
      { id: 'patio', name: 'Patio', color: '#10b981', tables: [3] },
    ];

    setTables(defaultTables);
    setZones(defaultZones);
  };

  const saveConfiguration = () => {
    const errors = validateConfiguration();
    if (errors.length > 0) {
      setValidationErrors(errors);
      return;
    }

    // Save configuration logic here
    console.log('Saving configuration...', { tables, zones });
    setValidationErrors([]);
    setIsDraftMode(false);
  };

  const validateConfiguration = (): string[] => {
    const errors: string[] = [];
    
    // Check for duplicate table numbers
    const tableNumbers = tables.map(t => t.number);
    const duplicateNumbers = tableNumbers.filter((num, index) => tableNumbers.indexOf(num) !== index);
    if (duplicateNumbers.length > 0) {
      errors.push(`Duplicate table numbers: ${duplicateNumbers.join(', ')}`);
    }

    // Check for overlapping tables
    for (let i = 0; i < tables.length; i++) {
      for (let j = i + 1; j < tables.length; j++) {
        const table1 = tables[i];
        const table2 = tables[j];
        
        if (
          table1.x < table2.x + table2.width &&
          table1.x + table1.width > table2.x &&
          table1.y < table2.y + table2.height &&
          table1.y + table1.height > table2.y
        ) {
          errors.push(`Tables ${table1.number} and ${table2.number} are overlapping`);
        }
      }
    }

    return errors;
  };

  const addTable = () => {
    const newTable: Table = {
      id: Math.max(...tables.map(t => t.id), 0) + 1,
      number: (tables.length + 1).toString(),
      seats: 4,
      status: 'available',
      zone: zones[0]?.id || 'main',
      shape: 'circle',
      x: 50,
      y: 50,
      width: 80,
      height: 80,
    };

    setTables(prev => [...prev, newTable]);
    setIsDraftMode(true);
  };

  const updateTable = (id: number, updates: Partial<Table>) => {
    setTables(prev => prev.map(table => 
      table.id === id ? { ...table, ...updates } : table
    ));
    setIsDraftMode(true);
  };

  const deleteTable = (id: number) => {
    setTables(prev => prev.filter(table => table.id !== id));
    setSelectedTable(null);
    setIsDraftMode(true);
  };

  const addZone = () => {
    const newZone: Zone = {
      id: `zone-${zones.length + 1}`,
      name: `Zone ${zones.length + 1}`,
      color: '#6366f1',
      tables: [],
    };

    setZones(prev => [...prev, newZone]);
    setIsDraftMode(true);
  };

  const updateZone = (id: string, updates: Partial<Zone>) => {
    setZones(prev => prev.map(zone => 
      zone.id === id ? { ...zone, ...updates } : zone
    ));
    setIsDraftMode(true);
  };

  const deleteZone = (id: string) => {
    // Move tables from deleted zone to unassigned
    setTables(prev => prev.map(table => 
      table.zone === id ? { ...table, zone: undefined } : table
    ));
    setZones(prev => prev.filter(zone => zone.id !== id));
    setIsDraftMode(true);
  };

  const resetLayout = () => {
    if (history.length > 0) {
      const lastState = history[history.length - 1];
      setTables(lastState.tables);
      setZones(lastState.zones);
      setHistory(prev => prev.slice(0, -1));
    }
  };

  const getTableStatusColor = (status: Table['status']) => {
    switch (status) {
      case 'available': return '#10b981';
      case 'occupied': return '#ef4444';
      case 'reserved': return '#f59e0b';
      case 'out-of-service': return '#6b7280';
      default: return '#3b82f6';
    }
  };

  return (
    <AppLayout title="Table Management">
      <div className="space-y-6">
        {/* Header */}
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-3xl font-bold bg-gradient-to-r from-blue-600 to-purple-600 bg-clip-text text-transparent">
              Table Management
            </h1>
            <p className="text-gray-600 mt-1">Design and manage your dining area layout</p>
          </div>
          
          <div className="flex items-center gap-3">
            {isDraftMode && (
              <Badge variant="outline" className="bg-yellow-50 text-yellow-700 border-yellow-300">
                <Clock className="w-3 h-3 mr-1" />
                Draft Mode
              </Badge>
            )}
            
            <Button 
              variant="outline" 
              onClick={resetLayout}
              disabled={history.length === 0}
              className="flex items-center gap-2"
            >
              <RotateCcw className="w-4 h-4" />
              Undo
            </Button>
            
            <Button 
              onClick={saveConfiguration}
              className="bg-gradient-to-r from-blue-600 to-purple-600 hover:from-blue-700 hover:to-purple-700 flex items-center gap-2"
            >
              <Save className="w-4 h-4" />
              Save Layout
            </Button>
          </div>
        </div>

        {/* Validation Errors */}
        {validationErrors.length > 0 && (
          <Card className="border-red-200 bg-red-50">
            <CardContent className="pt-4">
              <div className="flex items-start gap-3">
                <AlertTriangle className="w-5 h-5 text-red-500 flex-shrink-0 mt-0.5" />
                <div>
                  <h3 className="font-medium text-red-800 mb-2">Configuration Issues</h3>
                  <ul className="text-sm text-red-700 space-y-1">
                    {validationErrors.map((error, index) => (
                      <li key={index}>• {error}</li>
                    ))}
                  </ul>
                </div>
              </div>
            </CardContent>
          </Card>
        )}

        {/* Main Content */}
        <Tabs value={activeTab} onValueChange={setActiveTab} className="space-y-6">
          <TabsList className="grid w-full grid-cols-3">
            <TabsTrigger value="layout" className="flex items-center gap-2">
              <Layout className="w-4 h-4" />
              Layout Designer
            </TabsTrigger>
            <TabsTrigger value="zones" className="flex items-center gap-2">
              <Grid3x3 className="w-4 h-4" />
              Zone Management
            </TabsTrigger>
            <TabsTrigger value="settings" className="flex items-center gap-2">
              <Settings className="w-4 h-4" />
              Settings
            </TabsTrigger>
          </TabsList>

          {/* Layout Designer Tab */}
          <TabsContent value="layout" className="space-y-6">
            <div className="grid grid-cols-1 lg:grid-cols-4 gap-6">
              {/* Layout Canvas */}
              <Card className="lg:col-span-3">
                <CardHeader>
                  <div className="flex items-center justify-between">
                    <CardTitle className="flex items-center gap-2">
                      <Layout className="w-5 h-5" />
                      Dining Area Layout
                    </CardTitle>
                    <Button onClick={addTable} className="flex items-center gap-2">
                      <Plus className="w-4 h-4" />
                      Add Table
                    </Button>
                  </div>
                </CardHeader>
                <CardContent>
                  <div className="relative bg-gray-50 border-2 border-dashed border-gray-300 rounded-lg" style={{ height: '500px' }}>
                    {/* Render tables */}
                    {tables.map((table) => (
                      <div
                        key={table.id}
                        className={`absolute cursor-pointer transition-all duration-200 border-2 flex items-center justify-center text-sm font-medium text-white shadow-lg hover:shadow-xl ${
                          selectedTable?.id === table.id ? 'ring-2 ring-blue-500 ring-offset-2' : ''
                        }`}
                        style={{
                          left: table.x,
                          top: table.y,
                          width: table.width,
                          height: table.height,
                          backgroundColor: getTableStatusColor(table.status),
                          borderRadius: table.shape === 'circle' ? '50%' : table.shape === 'square' ? '8px' : '12px',
                        }}
                        onClick={() => setSelectedTable(table)}
                      >
                        {table.number}
                      </div>
                    ))}
                  </div>
                </CardContent>
              </Card>

              {/* Table Properties Panel */}
              <Card>
                <CardHeader>
                  <CardTitle className="text-lg">Table Properties</CardTitle>
                </CardHeader>
                <CardContent className="space-y-4">
                  {selectedTable ? (
                    <>
                      <div>
                        <label className="text-sm font-medium mb-1 block">Table Number</label>
                        <Input
                          value={selectedTable.number}
                          onChange={(e) => updateTable(selectedTable.id, { number: e.target.value })}
                        />
                      </div>
                      
                      <div>
                        <label className="text-sm font-medium mb-1 block">Seats</label>
                        <Input
                          type="number"
                          value={selectedTable.seats}
                          onChange={(e) => updateTable(selectedTable.id, { seats: parseInt(e.target.value) || 1 })}
                        />
                      </div>

                      <div>
                        <label className="text-sm font-medium mb-1 block">Status</label>
                        <select
                          value={selectedTable.status}
                          onChange={(e) => updateTable(selectedTable.id, { status: e.target.value as Table['status'] })}
                          className="w-full p-2 border border-gray-300 rounded-md"
                        >
                          <option value="available">Available</option>
                          <option value="occupied">Occupied</option>
                          <option value="reserved">Reserved</option>
                          <option value="out-of-service">Out of Service</option>
                        </select>
                      </div>

                      <div>
                        <label className="text-sm font-medium mb-1 block">Shape</label>
                        <select
                          value={selectedTable.shape}
                          onChange={(e) => updateTable(selectedTable.id, { shape: e.target.value as Table['shape'] })}
                          className="w-full p-2 border border-gray-300 rounded-md"
                        >
                          <option value="circle">Circle</option>
                          <option value="square">Square</option>
                          <option value="rectangle">Rectangle</option>
                        </select>
                      </div>

                      <Separator />

                      <Button
                        variant="destructive"
                        onClick={() => deleteTable(selectedTable.id)}
                        className="w-full"
                      >
                        Delete Table
                      </Button>
                    </>
                  ) : (
                    <p className="text-gray-500 text-center py-8">
                      Select a table to edit its properties
                    </p>
                  )}
                </CardContent>
              </Card>
            </div>
          </TabsContent>

          {/* Zone Management Tab */}
          <TabsContent value="zones" className="space-y-6">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              {zones.map((zone) => (
                <Card key={zone.id}>
                  <CardHeader>
                    <div className="flex items-center justify-between">
                      <CardTitle className="flex items-center gap-2">
                        <div
                          className="w-4 h-4 rounded"
                          style={{ backgroundColor: zone.color }}
                        />
                        {zone.name}
                      </CardTitle>
                      <Button
                        variant="destructive"
                        size="sm"
                        onClick={() => deleteZone(zone.id)}
                      >
                        Delete
                      </Button>
                    </div>
                  </CardHeader>
                  <CardContent className="space-y-4">
                    <div>
                      <label className="text-sm font-medium mb-1 block">Zone Name</label>
                      <Input
                        value={zone.name}
                        onChange={(e) => updateZone(zone.id, { name: e.target.value })}
                      />
                    </div>
                    
                    <div>
                      <label className="text-sm font-medium mb-1 block">Color</label>
                      <Input
                        type="color"
                        value={zone.color}
                        onChange={(e) => updateZone(zone.id, { color: e.target.value })}
                      />
                    </div>

                    <div>
                      <label className="text-sm font-medium mb-1 block">Tables in Zone</label>
                      <div className="flex flex-wrap gap-1">
                        {tables
                          .filter(table => table.zone === zone.id)
                          .map(table => (
                            <Badge key={table.id} variant="secondary">
                              Table {table.number}
                            </Badge>
                          ))}
                      </div>
                    </div>
                  </CardContent>
                </Card>
              ))}

              <Card className="border-dashed">
                <CardContent className="flex items-center justify-center py-12">
                  <Button onClick={addZone} className="flex items-center gap-2">
                    <Plus className="w-4 h-4" />
                    Add New Zone
                  </Button>
                </CardContent>
              </Card>
            </div>
          </TabsContent>

          {/* Settings Tab */}
          <TabsContent value="settings" className="space-y-6">
            <Card>
              <CardHeader>
                <CardTitle className="flex items-center gap-2">
                  <Settings className="w-5 h-5" />
                  Layout Settings
                </CardTitle>
              </CardHeader>
              <CardContent className="space-y-6">
                <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                  <div className="space-y-4">
                    <h3 className="font-medium">Import/Export</h3>
                    <div className="space-y-2">
                      <Button variant="outline" className="w-full flex items-center gap-2">
                        <Upload className="w-4 h-4" />
                        Import Layout
                      </Button>
                      <Button variant="outline" className="w-full flex items-center gap-2">
                        <Download className="w-4 h-4" />
                        Export Layout
                      </Button>
                    </div>
                  </div>

                  <div className="space-y-4">
                    <h3 className="font-medium">Quick Actions</h3>
                    <div className="space-y-2">
                      <Button variant="outline" className="w-full flex items-center gap-2">
                        <Sparkles className="w-4 h-4" />
                        Auto-Optimize Layout
                      </Button>
                      <Button variant="outline" className="w-full flex items-center gap-2">
                        <CheckCircle className="w-4 h-4" />
                        Validate Configuration
                      </Button>
                    </div>
                  </div>
                </div>
              </CardContent>
            </Card>
          </TabsContent>
        </Tabs>
      </div>
    </AppLayout>
  );
}

interface Table {
  id: string;
  x: number;
  y: number;
  width: number;
  height: number;
  shape: 'square' | 'round';
  capacity: number;
  status: 'available' | 'occupied' | 'reserved' | 'dirty';
  label: string;
}

export default function TableManagementPage() {
  // Simple button components for the interface
  const SaveDraftButton = () => (
    <Button variant="outline" size="sm" className="bg-gradient-to-r from-green-50 to-emerald-50 hover:from-green-100 hover:to-emerald-100 border-green-200">
      <Save className="w-4 h-4 mr-1" />
      Save Draft
    </Button>
  );

  const ActivateLayoutButton = () => (
    <Button size="sm" className="bg-gradient-to-r from-blue-600 to-purple-600 hover:from-blue-700 hover:to-purple-700">
      <Play className="w-4 h-4 mr-1" />
      Activate
    </Button>
  );

  // State management
  const [tables, setTables] = useState<Table[]>([]);
  const [selectedTool, setSelectedTool] = useState<'select' | 'table'>('select');
  const [selectedTable, setSelectedTable] = useState<Table | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [layoutName, setLayoutName] = useState('Default Layout');
  const [zones] = useState<any[]>([]);
  const [validationErrors] = useState<string[]>([]);
  const [isDraftMode] = useState<boolean>(false);
  const [history] = useState<any[]>([]);
  const [activeTab, setActiveTab] = useState('editor');

  useEffect(() => {
    loadTables();
  }, []);

  const loadTables = async () => {
    try {
      const response = await fetch('/api/tables');
      if (response.ok) {
        const data = await response.json();
        setTables(data);
      }
    } catch (error) {
      console.error('Failed to load tables:', error);
    } finally {
      setIsLoading(false);
    }
  };

  const addTable = (tableData: Partial<Table>) => {
    const newTable: Table = {
      id: Math.random().toString(36).substr(2, 9),
      x: 100,
      y: 100,
      width: 80,
      height: 80,
      shape: 'square',
      capacity: 4,
      status: 'available',
      label: `${tables.length + 1}`,
      ...tableData,
    };
    setTables([...tables, newTable]);
  };

  if (isLoading) {
    return (
      <AppLayout>
        <div className="flex items-center justify-center h-64">
          <div className="text-lg">Loading...</div>
        </div>
      </AppLayout>
    );
  }

  return (
    <AppLayout>
      <div className="flex flex-col h-screen bg-gradient-to-br from-blue-50 via-white to-purple-50">
        {/* Header */}
        <div className="bg-white/80 backdrop-blur-sm border-b border-gray-200/50 p-4 shadow-sm">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-4">
              <h1 className="text-2xl font-bold bg-gradient-to-r from-blue-600 to-purple-600 bg-clip-text text-transparent">
                Table Management
              </h1>
              <Input
                value={layoutName}
                onChange={(e) => setLayoutName(e.target.value)}
                className="w-64 bg-white/50 border-gray-200/50"
                placeholder="Layout name"
              />
            </div>
            <div className="flex items-center space-x-2">
              <SaveDraftButton />
              <ActivateLayoutButton />
            </div>
          </div>
        </div>

        {/* Main Content */}
        <div className="flex-1 flex overflow-hidden">
          {/* Canvas */}
          <div className="flex-1 p-4">
            <Card className="h-full">
              <CardContent className="p-0 h-full">
                <Tabs value={activeTab} onValueChange={setActiveTab} className="h-full flex flex-col">
                  <TabsList className="m-4 mb-0">
                    <TabsTrigger value="editor">Editor</TabsTrigger>
                    <TabsTrigger value="tools">Tools</TabsTrigger>
                  </TabsList>

                  <TabsContent value="editor" className="flex-1 p-4">
                    <div className="h-full bg-gradient-to-br from-gray-50 to-gray-100 border-2 border-dashed border-gray-200 rounded-xl relative overflow-hidden">
                      {/* Tables */}
                      {tables.map((table) => (
                        <div
                          key={table.id}
                          className={`absolute bg-white border-2 rounded-lg shadow-sm cursor-pointer transition-all duration-200 ${
                            selectedTable?.id === table.id
                              ? 'border-blue-500 shadow-lg scale-105'
                              : 'border-gray-300 hover:border-gray-400'
                          }`}
                          style={{
                            left: `${table.x}px`,
                            top: `${table.y}px`,
                            width: `${table.width}px`,
                            height: `${table.height}px`,
                          }}
                          onClick={() => setSelectedTable(table)}
                        >
                          <div className="flex items-center justify-center h-full text-xs font-medium text-gray-700">
                            Table {table.label}
                          </div>
                        </div>
                      ))}
                      
                      {/* Empty State */}
                      {tables.length === 0 && (
                        <div className="absolute inset-0 flex items-center justify-center text-gray-400">
                          <div className="text-center">
                            <Grid3x3 className="w-12 h-12 mx-auto mb-2 opacity-30" />
                            <p className="text-sm">Click "Add Table" to start designing your floor plan</p>
                          </div>
                        </div>
                      )}
                    </div>
                  </TabsContent>

                  <TabsContent value="tools" className="flex-1 p-4">
                    <div className="grid grid-cols-2 gap-4">
                      <Button
                        variant="outline"
                        onClick={() => addTable({ shape: 'square' })}
                        className="h-24 flex flex-col gap-2 border-dashed"
                      >
                        <div className="w-8 h-8 bg-blue-100 rounded"></div>
                        <span className="text-xs">Square Table</span>
                      </Button>
                      <Button
                        variant="outline"
                        onClick={() => addTable({ shape: 'round' })}
                        className="h-24 flex flex-col gap-2 border-dashed"
                      >
                        <div className="w-8 h-8 bg-green-100 rounded-full"></div>
                        <span className="text-xs">Round Table</span>
                      </Button>
                    </div>
                  </TabsContent>
                </Tabs>
              </CardContent>
            </Card>
          </div>

          {/* Sidebar */}
          <div className="w-80 p-4 border-l border-gray-200 bg-gray-50/50">
            <div className="space-y-4">
              {/* Table Properties */}
              {selectedTable ? (
                <Card>
                  <CardHeader className="pb-3">
                    <CardTitle className="text-sm">Table Properties</CardTitle>
                  </CardHeader>
                  <CardContent className="space-y-4">
                    <div className="grid grid-cols-2 gap-3">
                      <div>
                        <label className="text-xs font-medium text-gray-600">Capacity</label>
                        <Input 
                          type="number" 
                          value={selectedTable.capacity} 
                          className="h-8 text-xs" 
                        />
                      </div>
                      <div>
                        <label className="text-xs font-medium text-gray-600">Status</label>
                        <select className="w-full h-8 px-2 text-xs border rounded-md">
                          <option>Available</option>
                          <option>Occupied</option>
                          <option>Reserved</option>
                        </select>
                      </div>
                    </div>
                  </CardContent>
                </Card>
              ) : (
                <Card>
                  <CardContent className="text-center py-8">
                    <p className="text-sm text-gray-500">Select a table to edit properties</p>
                  </CardContent>
                </Card>
              )}

              {/* Layout Tools */}
              <Card>
                <CardHeader className="pb-3">
                  <CardTitle className="text-sm">Layout Tools</CardTitle>
                </CardHeader>
                <CardContent className="space-y-3">
                  <Button variant="outline" size="sm" className="w-full justify-start">
                    <Plus className="w-3 h-3 mr-2" />
                    Add Table
                  </Button>
                  <Button variant="outline" size="sm" className="w-full justify-start">
                    <Settings className="w-3 h-3 mr-2" />
                    Layout Settings
                  </Button>
                </CardContent>
              </Card>
            </div>
          </div>
        </div>
      </div>
    </AppLayout>
  );
}